import React from 'react';
import logo from './../logo.svg';
import './../assets/css/style.css';
import './../assets/css/colors.css';

function Home() {
    return (
        <section className="page bg-dark">
            <div className="home">
                <div className="intro">Hi, I am <span>Ebuka Maduekwe</span></div>
                <p className="page-p">
                    I am a focused and innovative software engineer who always tries to achieve excellence even in
                    conflicting priorities, while at the same time making sure the team attains excellence as well, because a
                    team is as strong as its weakest link. I equally attempt new experiences courageously and make sure I
                    respect the people I work with, the environment I work in and exhibit a great understanding and respect
                    of individual differences.
                </p>
            </div>
        </section>
    )
}

export default Home
