import React from 'react'
import './../assets/css/sidenav.css'
import logo from './../logo.svg';
import {FaTwitter} from "react-icons/fa"
import Button from './Button';

export default function SideNav() {
    return (
        <nav className="side-nav sbg-dark">
            <div className="nav-image side-nav-top">
                <img className="img-circle" src={logo} alt="Logo"/>
            </div>
            
            <div className="nav-list side-nav-top">
                <Button text="Home" url="/" type="btn-one"/>
                <Button text="Experience" url="/experience" type="btn-one"/>
                <Button text="Contact" url="/contact" type="btn-one"/>
            </div>
            <div className="footer">2020 &copy;</div>
        </nav>
    )
}
